.social {
  display: flex;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
}

.social__list {
  display: flex;
  list-style-type: none;
  width: 132px;
  justify-content: space-between;
  padding: 0;
  margin: 0;
}

.social__link {
  width: 28px;
  height: 22px;
  display: flex;
  outline: 2px solid green;
}
